import React from 'react';
import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  likeBlog,
  removeBlog,
  initializeBlogs,
  createComment
} from '../actions/blogActions';
import { setNotification } from '../actions/notificationActions';
import { useParams } from 'react-router-dom';

import { Button, Card, Form } from 'react-bootstrap';

const Blog = () => {
  const dispatch = useDispatch();

  const user = useSelector((state) => state.user);
  const blogs = useSelector((state) => state.blogs);

  const id = useParams().id;

  const blog = blogs.find((b) => b.id === id);

  const [content, setContent] = useState('');

  useEffect(() => {
    dispatch(initializeBlogs());
  }, [dispatch]);

  if (!blog) {
    return null;
  }

  const handleBlogLike = () => {
    dispatch(likeBlog(blog));
    dispatch(setNotification(`${blog.title} Liked!`));
  };
  const handleBlogRemove = () => {
    dispatch(removeBlog(blog));
    dispatch(setNotification(`${blog.title} Removed!`));
  };

  const handleNewComment = (e) => {
    e.preventDefault();
    dispatch(createComment(content, blog));
    dispatch(setNotification(`Added comment - ${content}!`));
    setContent('');
  };

  return (
    <div>
      <div>
        <Card style={{ width: '18rem' }}>
          <Card.Body>
            <Card.Title>{blog.title}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">
              by {blog.author}
            </Card.Subtitle>
            <Card.Text>
              <Card.Link href={blog.url}>{blog.url}</Card.Link>
              <br />
              {blog.likes} {blog.likes === 1 ? 'Person' : 'People'} like this
              blog
              <br />
            </Card.Text>
            <Button onClick={handleBlogLike} style={{ margin: '5px' }}>
              Like Blog
            </Button>
            {user.id === blog.user.id && (
              <Button onClick={handleBlogRemove}>remove</Button>
            )}
            <br />
          </Card.Body>
        </Card>

        <div>
          <br />
          <h3>Comments</h3>
          <Form onSubmit={handleNewComment}>
            <Form.Group>
              <Form.Control
                style={{ width: '40vw' }}
                type="text"
                placeholder="Enter Comment"
                onChange={(e) => setContent(e.target.value)}
                value={content}
              />
              <Button type="submit" style={{ marginTop: '5px' }}>
                Add Comment
              </Button>
            </Form.Group>
          </Form>
          <ul>
            {blog.comments.map((comment, i) => {
              return <li key={i}>{comment}</li>;
            })}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Blog;
