import blogService from '../services/blog';

export const initializeBlogs = () => async (dispatch) => {
  const blogs = await blogService.getAll();
  dispatch({
    type: 'INIT_BLOGS',
    data: blogs
  });
};

export const createBlog = (blog, token) => async (dispatch) => {
  const newBlog = await blogService.create(blog, token);
  dispatch({
    type: 'NEW_BLOG',
    data: newBlog
  });
};

export const likeBlog = (blog) => {
  return async (dispatch) => {
    const toLike = { ...blog, likes: blog.likes + 1 };
    const data = await blogService.update(toLike);

    dispatch({
      type: 'LIKE_BLOG',
      data
    });
  };
};

export const removeBlog = (blog) => {
  return async (dispatch) => {
    await blogService.remove(blog.id);
    dispatch({
      type: 'REMOVE_BLOG',
      data: blog
    });
  };
};

export const createComment = (comment, blog) => {
  return async (dispatch) => {
    const createdComment = await blogService.createComment(comment, blog);
    dispatch({
      type: 'NEW_COMMENT',
      data: createdComment
    });
  };
};
